.block {
    padding: 100px 0;
}

.block_title {
    text-align: center;
    font-size: 2rem;
    margin-bottom: 30px;
}

.block_first {
    background-color: grey;
}

#portfolio_two .mix,
#portfolio .mix {
    display: none;
}

.coolmixitup_one .controls {
    margin-bottom: 30px;
}

#filter-list1,
#filter-list {
    list-style: none;
    display: flex;
}

#filter-list1 li,
#filter-list li {
    margin-right: 5px;
}

#filter-list1 {
    flex-wrap: wrap;
}

#filter-list1 li {
    margin-bottom: 5px;
}


.coolmixitup_one .controls button {
    padding: 10px 20px;
    font-size: 1.4rem;
    background-color: orange;
    color: #fff;
    border: none;
    cursor: pointer;
}

.coolmixitup_one .controls button:focus,
.coolmixitup_one .controls button:hover,
.coolmixitup_one .controls button:active {
    background-color: darken(orange, 20%);
}

#portfolio_two li,
#portfolio li {
    width: 24.5%;
    border: 1px solid red;
    position: relative;
    cursor: pointer;
}

#portfolio_two li:before,
#portfolio li:before {
    opacity: 0.5;
    background-color: orange;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    position: absolute;
    content: " ";
}

#portfolio_two li img,
#portfolio_two li a img,
#portfolio li img,
#portfolio li a img {
    display: block;
    width: 100%;
    height: auto;
}

.port_link_two,
.port_link {
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    position: absolute;
    display: inline-block;
    width: 50px;
    height: 50px;
    background-size: cover;
    background-image: url(../img/search.png);
}

.mfp-arrow-left {
    background: url(data:image/svg+xml;utf8;base64,) no-repeat top left !important;
    width: 35px;
    height: 56px;
}

.mfp-arrow-right {
    background: url(data:image/svg+xml;utf8;base64,) no-repeat top right !important;
    width: 35px;
    height: 56px;
}

.mfp-arrow-left::before,
.mfp-arrow-right::before {
    display: none;
    content: ''
}

.mfp-arrow-left::after,
.mfp-arrow-right::after {
    display: none;
    content: ''
}